
import { defineComponent, PropType } from 'vue'
import * as API from '@/API'
import * as type from '@/types'
import * as music from '@/music'
import { skillDetails } from '@/global'
export default defineComponent({
    setup() {
        return {
            skillDetails,
        }
    },
    props: {
        playerProp: {
            type: Object as PropType<type.Player>,
            required: true,
            size: Number,
        },
        editAble: Boolean,
    },
    data() {
        return {
            player: {} as type.Player,
            dataLoad: false,
            hardCoreMode: false,
            editProfile: false,
            img: '',
            images: ['001', '002', '003', '004', '005'],
            message: '',
        }
    },
    mounted() {
        this.player = this.playerProp
        this.dataLoad = true
        if (this.editAble) {
            music.changeVolume(this.player.settings.musicVolume)
        }
    },

    computed: {
        registered() {
            let help = JSON.parse(this.player.registeredAt)
            return help
        },

        playTime() {
            let time = this.player.playedTime
            let unit = ' ticks'
            if (time / 60 > 1) {
                time /= 60 //sec
                unit = ' sec'
            }
            if (time / 60 > 1) {
                time /= 60 //min
                unit = ' min'
            }
            if (time / 60 > 1) {
                time /= 60 //hour
                unit = ' hours'
            }
            return time.toFixed(1) + unit
        },
    },

    methods: {
        checkUserName() {
            if (this.player.username == 'gast') {
                this.message = 'invalid username'
                return false
            } else {
                this.message = ''
                return true
            }
        },
        buttonSound() {
            music.ButtonSound(this.player.settings.effectVolume)
        },

        changeImg(id: string) {
            music.ButtonSound(this.player.settings.effectVolume)
            this.player.img = id
        },
        async toggleEdit(save: boolean) {
            music.ButtonSound(this.player.settings.effectVolume)
            this.editProfile = !this.editProfile
            if (save) {
                try {
                    await API.addPlayer(this.player)
                } catch {
                    API.logout()
                }
            }
        },
    },
})
